import React, { useState, useEffect, useRef } from 'react';
import { Paper, InputBase, IconButton, Box } from '@material-ui/core';
import { makeStyles, useTheme, withStyles } from '@material-ui/core/styles';
import { Search as SearchIcon } from '@material-ui/icons';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
  },
  input: {
    flex: 1,
    maxWidth: '100%',
  },
  iconButton: {
    padding: 10,
  },
}));

export default function Cari(props) {
  const { surah, ayat } = props;
  const classes = useStyles();
  const [keyword, setKeyword] = useState("");

  const handleSearch = (e) => {
    e.preventDefault();
    searchAyat(keyword);
  }

  const searchAyat = (keyword) => {
    const URL = `http://api.alquran.cloud/v1/search/${keyword}/all/id.indonesian`;
    return fetch(URL)
      .then(resp => {
        if (resp.ok) {
          return resp;
        } else {
          throw new Error("Error: " + resp.statusText);
        }
      })
      .then(resp => resp.json())
      .then(result => console.log(result))
      .catch(err => {
        console.log(err)
      })
  }

  const handleChange = (e) => {
    setKeyword(e.target.value)
  }

  return (
    <Box m={3}>
      <form onSubmit={handleSearch}>
        <Paper className={classes.root}>
          <InputBase
            className={classes.input}
            placeholder="Masukkan Kata Kunci"
            onChange={handleChange}
            inputProps={{ 'aria-label': 'cari ayat' }}
          />
          <IconButton type="submit" className={classes.iconButton} aria-label="search">
            <SearchIcon />
          </IconButton>
        </Paper>
      </form>
    </Box>
  );
}
